import orderBy from 'lodash/orderBy';
export default {
	props: {
		list: {
			default () {
				return [];
			},
			type: Array
		}
	},
	computed: {
		orderedList () {
			return orderBy(this.list, ['MasterLevel'], 'asc');
		}
	}
};
